import React, { useState } from 'react'
import './style5.css'
import Dropdown from './body-4-1'

export default () => {

    const [active1, setActive1] = useState( [
        {
            contentText:  "Cht liu sn phm ca Coolmate?",
            dropText: "c chn la k cng trn cc cht liu tt nht nhm mang li s thoi mi cho ngi mc, t si Cotton Compact, Cotton Organic, Spandex co gin hay Rubber bn b.",
            actived: false
        },
        {
            contentText:  "Coolmate c ca hng khng?",
            dropText: "S khc bit ca Coolmate vi cc thng hiu thi trang khc l chng ti khng c ca hng. Thay v u t hng trm triu lm ca hng tht to, th chng ti dng s tin  vo nng cao cht lng sn phm, v gim gi bn. Suy cho cng Coolmate ngh tin rng n ng khng thch phi mt hng gi  vo ca hng ch i mua my  mc c bn hng ngy u.",
            actived: false
        },
        {
            contentText:  "Chi ph ship hng nh th no?",
            dropText: "Tt c cc n hng trn 300.000VN u c min ph chi ph vn chuyn, trn ton quc.Gi bn thy trn website l tt c nhng g bn phi tr.",
            actived: false
        },
        {
            contentText:  "Cc sn phm c sn xut  u?",
            dropText: "100% sn phm Coolmate c sn xut ti Vit Nam. Chng ti hp tc vi nhiu nh thit k t Nht Bn, Hn Quc  i mi kiu dng cng nh cp nht cng ngh sn xut mi, v tt c s c gia cng trong xng ca chng ti ti Vit Nam.",
            actived: false
        },
        {
            contentText:  "Sn phm mua ri c c i tr hay khng?",
            dropText: "C. Chng ti c chnh sch i tr trong vng 45 ngy, v bt c l do g. Tm hiu thm",
            actived: false
        },
        {
            contentText:  "Ti c th mua l tng sn phm khng?",
            dropText: "Tt nhin l c. Hnh thc mua theo box l cch chng ti khuyn khch bn mua nhiu hn vi gi r hn. Chng ti vn lun c tng sn phm l trn website. Bn hy chn mn  mn mua trn thanh menu Sn Phm.",
            actived: false
        },
        {
            contentText:  "Bao lu k t khi t hng thnh cng ti s nhn c hng?",
            dropText: "- Ti H Ni: 2-4h nu bn t hng giao ni thnh trong thi gian t 8h sng n 8h ti.Sau 1 ngy nu bn  ngoi thnh HN. - Ti H Ch Minh: Bn s nhn c hng sau 1-3 ngy. - Ti tnh thnh khc: Bn s nhn c hng sau 2-4 ngy lm vic.",
            actived: false
        }
    ])

    const list = active1

    function onclick(item,index){
        return setActive1([
            ...list.slice(0,index),
            {
              ...item,
              actived: !item.actived
            },
            ...list.slice(index + 1)
          ])
    }

    let valueIndex = 0
    return (    
        <div className="dropdown-text">
            <span className="content-text">NHNG CU HI THNG GP</span>
            <div className="wrap-dropdown-text">
            {list.map((item,index)=>{   
                valueIndex = index + 1 
                if(index < 10){
                    valueIndex =  "0" + valueIndex + "."
                }
                return(<Dropdown valueIndex={valueIndex} item={item} index={index} onclicks={()=>{onclick(item,index)}} />)
            })}
            </div>
        </div>
    )
}